<template>
  <div class="from">
    <div class="templateImportBox">
      <el-button type="primary" @click="centerDialogVisible = true">
        
        <i class="el-icon-upload el-icon--right"></i>
      </el-button>
    </div>
    <!--  -->
    <div class="templateImportBox">
      <el-tag class="editor" @click="setCenter"></el-tag>
    </div>
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      width="600px"
      :before-close="handleClose"
    >
      <el-transfer
        filterable
        :filter-method="filterMethod"
        filter-placeholder=""
        v-model="rightValue"
        :data="data2"
        :titles="['', '']"
        @change="centerChange"
      ></el-transfer>
      <span slot="footer" class="dialog-footer">
        <!-- <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="dialogVisible = false"> </el-button>-->
      </span>
    </el-dialog>

    <el-dialog
      title=""
      :visible.sync="centerDialogVisible"
      width="30%"
      center
      style="font-family:PingFang SC"
    >
      <el-upload
        class="upload-demo"
        ref="upload"
        :action="excelUp"
        :on-preview="handlePreview"
        :on-remove="handleRemove"
        :file-list="fileList"
        :auto-upload="false"
        :on-change="handleChange"
      >
        <el-button slot="trigger" size="small" type="primary"></el-button>
        <el-button
          style="margin-left: 10px;"
          size="small"
          type="success"
          @click="submitUpload"
        ></el-button>
        <div slot="tip" class="el-upload__tip"></div>
      </el-upload>
      <span slot="footer" class="dialog-footer">
        <el-button @click="centerDialogVisible = false"> </el-button>
        <el-button type="primary" @click="centerDialogVisible = false"> </el-button>
      </span>
    </el-dialog>

    <el-table :data="tableData" ref="multipleTable" style="cursor: pointer;width: 780px;margin:0px auto;" >
      <el-table-column label="" width="140">
        <template slot-scope="scope">
          <span @click="toSingePoint(scope.row)">{{ scope.row.nodeName }}</span>
        </template>
      </el-table-column>
      <el-table-column label="" width="200">
        <template slot-scope="scope">
          <!-- <el-popover trigger="hover" placement="top">
            <p>: {{ scope.row.name }}</p>
            <p>: {{ scope.row.address }}</p>
            <div slot="reference" class="name-wrapper">
              <el-tag size="medium">{{ scope.row.name }}</el-tag>
            </div>
          </el-popover>-->
          <span @click="toSingePoint(scope.row)">{{ scope.row.nodeAddress }}</span>
        </template>
      </el-table-column>
      <el-table-column label="" width="100">
        <template slot-scope="scope">
          <span @click="toSingePoint(scope.row)">{{ scope.row.lng }}</span>
        </template>
      </el-table-column>
      <el-table-column label="" width="100">
        <template slot-scope="scope">
          <span @click="toSingePoint(scope.row)">{{ scope.row.lat }}</span>
        </template>
      </el-table-column>
      <!-- <el-table-column type="selection" width="30"></el-table-column> -->
      <el-table-column label="" width="120px">
        <template slot-scope="scope">
          <span @click="toSingePoint(scope.row)">{{ isCenter(scope.row.isCenter) }}</span>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <!-- <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button> -->
          <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-pagination
      style="display: flex;justify-content: center;margin-top:40px;"
      class="pagination"
      background
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page.sync="currentPage"
      :page-size="100"
      layout="prev, pager, next, jumper"
      :total="1000"
    ></el-pagination>
    <footer-footer />
  </div>
</template>

<script>
import FooterFooter from "./Footer.vue";
export default {
  name: "Index",
  data() {
    return {
      currentPage: 1,
      excelUp: "", //excel
      centerDialogVisible: false,
      dialogVisible: false, //
      data2: [],
      rightValue: [],
      filterMethod(query, item) {
        return item.pinyin.indexOf(query) > -1;
      },
      centerPoint: [],
      serverPoint: [],
      checked: false,
      multipleSelection: [],
      questionId: "",
      tableData: [],
      fileList: [
        // {
        //   name: "food.jpeg",
        //   url:
        //     "https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100"
        // },
        // {
        //   name: "food2.jpeg",
        //   url:
        //     "https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100"
        // }
      ]
    };
  },
  components: {
    FooterFooter,
  },
  methods: {
    // handleEdit (index, row) {
    //   console.log(index, row)
    // },
    /*

*/
    handleDelete(index, row) {
      let that = this;
      console.log(index, row);
      console.log(this.tableData[index]);
      this.$axios
        .delete(that.$url + "/node/deleteNode", {
          params: {
            questionId: that.questionId,
            nodeId: that.tableData[index].nodeId
          }
        })
        .then(function(response) {
          console.log(response);
          if (response.data.status == 0) {
          that.$notify({
            title: "",
            message: "" + that.tableData[index].nodeName + "",
            type: "success"
          });
          that.tableData.splice(index, 1);
          } else {
            that.$notify({
            title: "",
            message: response.data.msg,
            type: "warning"
          });
          }

        })
        .catch(function(error) {
          console.log(error);
        });
    },
    /*

*/
    // addPrograme(value) {
    //   console.log(value);
    //   this.tableData.unshift(value);
    // },
    handleSizeChange(val) {
      console.log(` ${val} `);
    },
    handleCurrentChange(val) {
      console.log(`: ${val}`);
    },
    /*

*/
    prepareData(index, row) {
      console.log(index, row);
      this.$router.push({ path: "/DataPerpare", query: { id: "1" } });
    },
    //
    submitUpload() {
      console.log();
      this.$refs.upload.submit();
    },
    handleChange(file, fileList) {
      let that = this;
      this.fileList = fileList.slice(-3);
      console.log(that.fileList);
      if (that.fileList[0].status == "success") {
        if (that.fileList[0].response.status == 0) {
          that.getAllpoinit();
        }
      }
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    },

    //
    getAllpoinit() {
      let that = this;
      this.$axios
        .get(this.$url + "/node/getQuestionNodes", {
          params: {
            questionId: that.questionId
          }
        })
        .then(res => {
          if (res.data.status == 0) {
          //
          for (let i in that.tableData) {
            if (that.tableData[i].isCenter == 0) {
            } else {
              that.rightValue.push(parseInt(i));
            }
          }
          console.log("server" + that.serverPoint);
          console.log("center" + that.centerPoint);
          } else {
            that.$notify({
              title: "",
              message: response.data.msg,
              type: "warning"
            });
          }
          console.log(res);
          that.tableData = res.data.object;


        })
        .then(data => {});
    },
    //
    isCenter(value) {
      return value == 0 ? "" : "";
    },
    //
    handleClose(done) {
      this.$confirm("")
        .then(_ => {
          done();
        })
        .catch(_ => {});
    },
    generateData2(arr) {
      console.log(arr);
      let data = [];
      //let cities = ["", "", "", "", "", "", ""];
      let cities = [];
      for (let i in arr) {
        cities.push(arr[i].nodeName);
      }
      let pinyin = cities;
      cities.forEach((city, index) => {
        data.push({
          label: city,
          key: index,
          pinyin: pinyin[index]
        });
      });
      this.data2 = data;
    },
    setCenter() {
      this.getAllpoinit();
      this.dialogVisible = true;
      this.generateData2(this.tableData);
    },
    centerChange(value, direction, movedKeys) {
      console.log(value, direction, movedKeys);
      if (direction == "right") {
        console.log("");
        console.log(movedKeys);
        this.changeCenter(movedKeys);
      } else {
        console.log("");
        this.changeServer(movedKeys);
      }
    },
    updateNode(res,isCenter) {
      console.log(res);
      let that = this;
      this.$axios
        .patch(
          this.$url + "/node/updateNode",
             that.$qs.stringify({
              nodeId: res.nodeId,
              questionId: that.questionId,
              nodeName: res.nodeName,
              nodeAddress: res.nodeAddress,
              lat: res.lat,
              lng: res.lng,
              isCenter: isCenter
          }),
        )
        .then(res1 => {
          console.log(111111111);
          console.log(res1);
          if (res1.data.status == 0) {
            console.log(11111111);
            that.$notify({
            title: '',
            message: ''+ res.nodeName + '',
            type: 'success'
        });
                  that.getAllpoinit();
          }  else {
            that.$notify({
              title: "",
              message: response.data.msg,
              type: "warning"
            });
          }

        })
          .catch(function (error) {
            this.$notify.error({
          title: '',
          message: ''
        });
  })
    },
    //
    changeCenter(movedKeys) {

      let that = this;
      for (let i in movedKeys) {
        console.log(movedKeys[i]);
        that.updateNode(that.tableData[movedKeys[i]],'1');
      }
    },
    //
    changeServer(movedKeys) {
      let that = this;
      for (let i in movedKeys) {
        console.log(movedKeys[i]);
        that.updateNode(that.tableData[movedKeys[i]],'0');
      }
    },
    //
    toSingePoint (row) {
      this.$router.push({
        path: "/SinglePoint",
        query: { 
          lat: row.lat,
          lng: row.lng,
          nodeAddress: row.nodeAddress
        }
      });
    }
  },
  beforeCreate() {

  },
  mounted() {
    let that = this;
    that.excelUp = that.$url + "/node/excelNodeInfo/" + this.$route.query.id;
    console.log(that.excelUp);
    that.questionId = this.$route.query.id;
    that.getAllpoinit();
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.from {
  margin: 0px 100px;
}
.mouseIn {
  cursor: pointer;
}
.templateImportBox {
  /* position: absolute;
  left: 50px;
  width: 250px;
  height: 200px;
  top: 100px;
  margin: 0px auto;
  display: flex;
  justify-content: flex-start;
  z-index: 2; */
  width: 800px;
  margin: 0px auto;
  display: flex;
  justify-content: flex-end;
  margin-bottom: 20px;
}
.editor {
  cursor: pointer;
}
.pagination {
  margin-top: 30px;
  margin-bottom: 100px;
}
</style>



