<template>
  <div class="login">
<el-card class="box-card">
  <div slot="header" class="clearfix">
    <span></span>
    <el-button style="float: right; padding: 3px 0" type="text" @click="toLogin"></el-button>
  </div>
    <el-form
      :model="ruleForm2"
      status-icon
      :rules="rules2"
      ref="ruleForm2"
      label-width="100px"
      class="demo-ruleForm"
    >
      <el-form-item label="" prop="userName">
        <el-input type="user" v-model="ruleForm2.userName" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="pass">
        <el-input type="password" v-model="ruleForm2.pass" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="checkPass">
        <el-input type="password" v-model="ruleForm2.checkPass" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item class="verifyCodeBox" label="" prop="email">
        <el-input type="user" v-model="ruleForm2.email" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item class="verifyCodeBox" label="" prop="emailCode">
        <el-input v-model="ruleForm2.emailCode">
          <el-button  class="getCode" slot="append" @click="getCode"></el-button>
        </el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm2')"></el-button>
        <el-button @click="resetForm('ruleForm2')"></el-button>
      </el-form-item>
    </el-form>
</el-card>

  </div>
</template>

<script>
export default {
  name: "Login",

  data() {
    //
    var checkEmail= (rule, value, callback) => {
      if (!value) {
        return callback(new Error(""));
      }
      setTimeout(() => {
        let reg=/^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$/;    
        if (!reg.test(value)) {
          callback(new Error(""));
        } else {
            callback();
        }
      }, 500);
    };
    //
    var user = (rule, value, callback) => {
      if (!value) {
        return callback(new Error(""));
      }
      setTimeout(() => {  
        let reg=/^[\u0391-\uFFE5]+$/;//   
        // let reg=/^[0-9]*$/;  
        if (!reg.test(value)) {
          callback(new Error(""));
        } else {
            callback();
        }
      }, 500);
    };
    //
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else {
        if (this.ruleForm2.checkPass !== "") {
          this.$refs.ruleForm2.validateField("checkPass");
        }
        callback();
      }
    };
    //
    var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else if (value !== this.ruleForm2.pass) {
          callback(new Error('!'));
        } else {
          callback();
        }
   };
    //
    var checkEmailCode = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    return {
      ruleForm2: {
        userName: '',  //
        pass: "",      //
        checkPass: "", //
        email: '',     //
        emailCode: '', //
      },
      rules2: {
        userName: [{ validator: user, trigger: "blur" }],
        pass: [{ validator: validatePass, trigger: "blur" }],
        checkPass: [
            { validator: validatePass2, trigger: 'blur' }
          ],
        email: [{ validator: checkEmail, trigger: "blur" }],
        emailCode: [{ validator: checkEmailCode, trigger: "blur" }]
      },
      verifyCodeURL: '',//
    };
  },
  methods: {
    //
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    //
    toLogin () {
        this.$router.push({path: '/Login'})
    },
    //
    submitForm(formName) {
      let that = this;
      this.$refs[formName].validate(valid => {
        if (valid) {
          //alert("submit!");
          that.rejest();
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    //
    getCode () {
      let that = this;
      if ( that.ruleForm2.email == '') {
        that.$notify({
          title: '',
          message: '',
          type: 'warning'
        });
      } else {
        //alert(that.ruleForm2.email);
        that.$axios.get(that.$url + "/userSystem/user/eMailCode", {
          params: {
            eMail: that.ruleForm2.email
          }
        })
        .then(function (response) {
          console.log(response);
        })
        .catch(function (error) {
          console.log(error);
        });
      }
    },
    //
    rejest () {
      let that = this;
        this.$axios
          .post(
             this.$url + "/userSystem/user",
              that.$qs.stringify({
                userName: that.ruleForm2.userName,
                password: that.ruleForm2.pass,
                rePassword: that.ruleForm2.checkPass,
                eMail : that.ruleForm2.email,
                mailCode : that.ruleForm2.emailCode,
              })
          )
          .then(res => {
            console.log(res);
            if (res.data.status == 0) {
              //state
              this.$store.commit("loginState", {
                userId: res.data.object.userId,
                userName: res.data.object.userName
              });
              this.$router.push({path: '/'})
            } else if (res.data.status == 1) {
              this.$alert("!", "", {
                confirmButtonText: "",
                type: "error"
              });
            } else if (res.data.status == 3) {
              this.$alert("", "", {
                confirmButtonText: "",
                type: "error"
              });
              this.verifyCode(); //
            } else {
              this.$alert("", "", {
                confirmButtonText: "",
                type: "error"
              });
            }
          });
    },
    //
    Login () {
      let that = this;
        console.log(that.ruleForm2.userName);
        this.$axios
          .post(
             this.$url + "/userSystem/session/user",
              that.$qs.stringify({
                userName: that.ruleForm2.userName,
                password: that.ruleForm2.pass,
                code: that.ruleForm2.verifyCode
              })
          )
          .then(res => {
            console.log(res);
            if (res.data.status == 0) {
              //state
              // this.$store.commit("loginState", {
              //   userId: this.userName,
              //   userName: res.data.userMessage.userName
              // });
              this.$router.push({path: '/'})
            } else if (res.data.status == 1) {
              this.$alert("!", "", {
                confirmButtonText: "",
                type: "error"
              });
            } else if (res.data.status == 3) {
              this.$alert("", "", {
                confirmButtonText: "",
                type: "error"
              });
              this.verifyCode(); //
            } else {
              this.$alert("", "", {
                confirmButtonText: "",
                type: "error"
              });
            }
          });
    },
  },
  mounted () {

  },

};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.login {
    margin-top: 100px;
}
  .text {
    font-size: 14px;
  }

  .item {
    margin-bottom: 18px;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }

  .box-card {
    width: 480px;
    margin: 0px auto;
  }

  .verifyCode {
    margin-left: 100px;
    cursor: pointer;
  }

  .getCode {
    cursor: pointer;
  }
</style>



