<template>
  <div class="from">
    <el-table :data="tableData" style="width: 1000px;margin:0px auto;">
      <el-table-column label="ID" width="140">
        <template slot-scope="scope">
          <span>{{ scope.row.id }}</span>
        </template>
      </el-table-column>
      <el-table-column label="" width="140">
        <template slot-scope="scope">
          <!-- <el-popover trigger="hover" placement="top">
            <p>: {{ scope.row.name }}</p>
            <p>: {{ scope.row.address }}</p>
            <div slot="reference" class="name-wrapper">
              <el-tag size="medium">{{ scope.row.name }}</el-tag>
            </div>
          </el-popover>-->
          <span>{{ scope.row.questionName }}</span>
        </template>
      </el-table-column>
      <el-table-column label="" width="140">
        <template slot-scope="scope">
          <span
            @click="prepareData(scope.$index, scope.row)"
            class="mouseIn"
          >{{ scope.row.prepareDataState}}</span>
        </template>
      </el-table-column>
      <el-table-column label="" width="140">
        <template slot-scope="scope">
          <span
            class="mouseIn"
            @click="workDistance(scope.$index, scope.row)"
          >{{ scope.row.checkDataState }}</span>
        </template>
      </el-table-column>
      <el-table-column label="" width="140">
        <template slot-scope="scope">
          <span
            class="mouseIn"
            @click="executionAlgorithm(scope.$index, scope.row)"
          >{{ scope.row.ExecutionAlgorithmState }}</span>
        </template>
      </el-table-column>
      <el-table-column label="" width="140">
        <template slot-scope="scope">
          <span class="mouseIn" @click="theResult(scope.$index, scope.row)">{{ scope.row.resultsState }}</span>
        </template>
      </el-table-column>





      <el-table-column label="">
        <template slot-scope="scope">
          <!-- <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button> -->
          <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      style="display: flex;justify-content: center;margin-top:40px;"
      background
      class="pagination"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page.sync="currentPage"
      :page-size="100"
      layout="prev, pager, next, jumper"
      :total="1000"
    ></el-pagination>





    <!--  -->
    <el-dialog title="" :visible.sync="dialogVisible" width="350px" :before-close="handleClose">
      <el-select v-model="value" placeholder="">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        ></el-option>
      </el-select>
      <el-button type="primary" @click="implement"></el-button>
      <el-table :data="yizhixing" style="300px" v-if="yizhixing.length != 0">
        <el-table-column label="" width="150">
          <template slot-scope="scope">
            <span class="mouseIn">{{ scope.row.value }}</span>
          </template>
        </el-table-column>
          <el-table-column  width="150">
          <template slot-scope="scope">
            <span class="mouseIn" style="display:none">{{ scope.row.key }}</span>
          </template>
          </el-table-column>
      </el-table>
      <el-table :data="zhengzaizhixing" style="300px" v-if="zhengzaizhixing.length != 0">
        <el-table-column label="" width="150">
          <template slot-scope="scope">
            <span class="mouseIn">{{ scope.row.value }}</span>
          </template>
        </el-table-column>
        <el-table-column  width="150">
          <template slot-scope="scope">
            <span class="mouseIn" style="display:none">{{ scope.row.key }}</span><span class="mouseIn" @click="stopcarry(scope.$index, scope.row)"></span>
          </template>
          </el-table-column>
      </el-table>
    </el-dialog>

      <!--  -->
      <el-dialog
        title=""
        :visible.sync="dialogVisible1"
        width="350px"
        :before-close="handleClose">
      <el-table :data="yizhixing" style="250" v-if="yizhixing.length != 0">
        <el-table-column label="" width="150">
          <template slot-scope="scope">
            <span class="mouseIn">{{ scope.row.value }}</span>
          </template>
        </el-table-column>
          <el-table-column  width="50">
          <template slot-scope="scope">
            <span class="mouseIn" style="display:none">{{ scope.row.key }}</span> <span class="mouseIn" @click="checkAlgorithm(scope.$index, scope.row)"></span>
          </template>
          </el-table-column>
      </el-table>
      <!-- <el-table :data="zhengzaizhixing" style="300px" v-if="zhengzaizhixing.length != 0">
        <el-table-column label="" width="150">
          <template slot-scope="scope">
            <span class="mouseIn">{{ scope.row.value }}</span>
          </template>
        </el-table-column>
        <el-table-column  width="150">
          <template slot-scope="scope">
            <span class="mouseIn" style="display:none">{{ scope.row.key }}</span><span class="mouseIn" @click="stopcarry(scope.$index, scope.row)"></span>
          </template>
          </el-table-column>
      </el-table> -->
      </el-dialog>

  </div>
</template>

<script>
export default {
  name: "Index",
  data() {
    return {
      currentPage: 1,
      tableData: [],
      list: [], //
      dialogVisible: false,
      options: [],
      valueId: "", //ID
      value: "",
      workIndex: '',//index
      yizhixing: [
      ],
      zhengzaizhixing: [
      ],
      //
      dialogVisible1: false,
      resultId: '',  //Id
    };
  },
  methods: {
    // handleEdit (index, row) {
    //   console.log(index, row)
    // },
    /*

*/
    handleDelete(index, row) {
      let that = this;
      console.log(index, row);
      console.log(that.tableData[index].id);
      this.$axios
        .delete(that.$url + "/question/removeQuestion", {
          params: {
            questionId: that.tableData[index].id
          }
        })
        .then(function(response) {
          console.log(11111111111111111);
          console.log(response);
          if (response.data.status == 0) {
            that.$notify({
              title: "",
              message: "*" + that.tableData[index].questionName + "*",
              type: "warning"
            });
            that.tableData.splice(index, 1);
          } else {
            that.$notify({
              title: "",
              message: response.data.msg ,
              type: "warning"
            });
          }

        })
        .catch(function(error) {
          console.log(error);
        });
    },
    /*

*/
    addPrograme(value) {
      //console.log(value)
      this.loadAllData();
      // console.log(value);
    },
    handleSizeChange(val) {
      console.log(` ${val} `);
    },
    handleCurrentChange(val) {
      console.log(`: ${val}`);
    },
    /*

*/
    prepareData(index, row) {
      let that = this;
      this.$router.push({
        path: "/DataPerpare",
        query: { id: that.tableData[index].id }
      });
    },

    //
    dealData(value) {
      console.log(parseInt(value.processState));
      console.log(parseInt(value.geneticExecuted));
      console.log(
        parseInt(value.processState) == 4 && parseInt(value.geneticExecuted) < 5
      );
      if (parseInt(value.processState) == 0) {
        let a = {
          id: value.questionId,
          questionName: value.questionName,
          prepareDataState: "", //
          checkDataState: "", //
          ExecutionAlgorithmState: "", //
          resultsState: ""
        };
        return a;
      } else if (parseInt(value.processState) == 1) {
        let a = {
          id: value.questionId,
          questionName: value.questionName,
          prepareDataState: "", //
          checkDataState: "", //
          ExecutionAlgorithmState: "", //
          resultsState: ""
        };
        return a;
      } else if (parseInt(value.processState) == 2) {
        console.log('lalalalla');
        let a = {
          id: value.questionId,
          questionName: value.questionName,
          prepareDataState: "", //
          checkDataState: "", //
          ExecutionAlgorithmState: "", //
          resultsState: ""
        };
        return a;
      } else if (parseInt(value.processState) == 3) {
        let a = {
          id: value.questionId,
          questionName: value.questionName,
          prepareDataState: "", //
          checkDataState: "", //
          ExecutionAlgorithmState: "", //
          resultsState: ""
        };
        return a;
      } else if (
        parseInt(value.simpleExecuted) == 8 ||
        parseInt(value.geneticExecuted) == 6
      ) {
        let a = {
          id: value.questionId,
          questionName: value.questionName,
          prepareDataState: "", //
          checkDataState: "", //
          ExecutionAlgorithmState: "", //
          resultsState: ""
        };
        return a;
      } else if (parseInt(value.processState) == 4) {
        console.log('sdafdsafdasfds')
        let a = {
          id: value.questionId,
          questionName: value.questionName,
          prepareDataState: "", //
          checkDataState: "", //
          ExecutionAlgorithmState: "", //
          resultsState: ""
        };
        return a;
      }
    },
    //
    workDistance(index, row) {
      this.workIndex = index;
      console.log(this.tableData[index]);
      console.log(this.list.length);
      console.log(index);
      let length = this.list.length - index - 1;
      console.log(length);
      if (parseInt(this.list[length].processState) == 2) {
        console.log(this.list[index]);
        this.$notify({
          title: "",
          message: ",",
          type: "success"
        });
        let that = this;

        that.$axios
          .get(that.$url + "/distance/generateDistance", {
            params: {
              questionId: that.tableData[index].id
            }
          })
          .then(function(response) {
            console.log(response);
            if (response.data.status == 0) {
              // that.$notify({
              //   title: "",
              //   message: "",
              //   type: "success"
              // });
              // that.$router.go(0);
            }  else {
            that.$notify({
              title: "",
              message: response.data.msg ,
              type: "warning"
            });
          }
            //that.loadAllData(); //
          })
          .catch(function(error) {
            console.log(error);
          });
          setTimeout(() => {
            that.loadAllData(); //
          },3000)

      } else {
            that.$notify({
              title: "",
              message: '' ,
              type: "warning"
            });
      }
      
    },
    //
    loaded() {
      let that = this;
      that.$axios
        .get(that.$url + "/getExecutedAlgorithm", {
          params: {
            questionId: that.valueId,
          }
        })
        .then(function(response) {
          console.log(response);
          if (response.data.status == 0) {
            that.yizhixing = [];
            for (let i in response.data.object) {
                let arr =  {
                value: response.data.object[i],
                key: i
              }
              that.yizhixing.unshift(arr);
            }
          } else {
            that.$notify({
              title: "",
              message: response.data.msg ,
              type: "warning"
            });
          }


        })
        .catch(function(error) {
          console.log(error);
        });
    },
    //
    loading() {
      let that = this;
      that.$axios
        .get(that.$url + "/getExecutingAlgorithm", {
          params: {
            questionId: that.valueId,
          }
        })
        .then(function(response) {
          if (response.data.status == 0) {
          console.log(response);
          that.zhengzaizhixing = [];
          for (let i in response.data.object) {
              let arr =  {
              value: response.data.object[i],
              key: i
            }
            that.zhengzaizhixing.unshift(arr);
          }
          }  else {
            that.$notify({
              title: "",
              message: response.data.msg ,
              type: "warning"
            });
          }


        })
        .catch(function(error) {
          console.log(error);
        });
    },
    //
    executionAlgorithm(index, row) {
      let that = this;
      that.valueId = that.tableData[index].id;
      that.dialogVisible = true;
      //
      that.$axios
        .get(that.$url + "/getNotExecutingAlgorithm", {
          params: {
            questionId: that.tableData[index].id
          }
        })
        .then(function(response) {
          if (response.data.status == 0) {
            that.options = [];
            for (let i in response.data.object) {
              // console.log(i);
              // console.log(response.data.object[i]);
              // console.log(Object.keys(response.data.object[i]))
              let arr = {
                value: i,
                label: response.data.object[i]
              };
              that.options.unshift(arr);
            }
          } else {
            that.$notify({
              title: "",
              message: response.data.msg ,
              type: "warning"
            });
          }

        })
        .catch(function(error) {
          console.log(error);
        });

        //
        that.loaded();

        //
        that.loading();
    },


    //
    flashH() {
      let that = this;
      //
      that.$axios
        .get(that.$url + "/getNotExecutingAlgorithm", {
          params: {
            questionId: that.valueId
          }
        })
        .then(function(response) {
          if (response.data.status == 0) {
          that.options = [];
          for (let i in response.data.object) {
            // console.log(i);
            // console.log(response.data.object[i]);
            // console.log(Object.keys(response.data.object[i]))
            let arr = {
              value: i,
              label: response.data.object[i]
            };
            that.options.unshift(arr);
          }
          }   else {
            that.$notify({
              title: "",
              message: response.data.msg ,
              type: "warning"
            });
          }

        })
        .catch(function(error) {
          console.log(error);
        });

      //
        that.loaded();

        //
        that.loading();
    },
    //
    implement() {
      let that = this;
      console.log(that.valueId);
      that.$notify({
        title: "",
        message: ",",
        type: "success"
      });
      if (that.value != "") {
        that.$axios
          .get(that.$url + "/executeAlgorithm", {
            params: {
              questionId: that.valueId,
              key: that.value
            }
          })
          .then(function(response) {
            console.log(response);
            if (response.data.status == 0) {
              // that.$notify({
              //   title: "",
              //   message: ",",
              //   type: "success"
              // });
              // that.loadAllData(); //
              // console.log(that.workIndex);
              // that.flashH();  //
            } else {
            that.$notify({
              title: "",
              message: response.data.msg ,
              type: "warning"
            });
          }
          })
          .catch(function(error) {
            console.log(error);
          });
      }
      console.log(that.value);
    },
    //
    stopcarry(index,row) {
      console.log(index);
      let that = this;
      console.log(this.zhengzaizhixing[index].key);
              that.$axios
          .get(that.$url + "/stopAlgorithm", {
            params: {
              questionId: that.valueId,
              key: that.zhengzaizhixing[index].key
            }
          })
          .then(function(response) {
            console.log(response);
            if (response.data.status == 0) {
              // that.$notify({
              //   title: "",
              //   message: ''+ that.zhengzaizhixing[index].value +'',
              //   type: "success"
              // });
              // console.log(that.workIndex);
              // that.flashH();  //
            } else {
            that.$notify({
              title: "",
              message: response.data.msg ,
              type: "warning"
            });
          }
          })
          .catch(function(error) {
            console.log(error);
          });
    },
    //
    stillcarry(index,row) {
      // console.log(index);
      // let that = this;
      // console.log(this.zhengzaizhixing[index].key);
      //         that.$axios
      //     .get(that.$url + "/stopAlgorithm", {
      //       params: {
      //         questionId: that.valueId,
      //         key: that.zhengzaizhixing[index].key
      //       }
      //     })
      //     .then(function(response) {
      //       console.log(response);
      //       if (response.data.status == 0) {
      //         that.$notify({
      //           title: "",
      //           message: ''+ that.zhengzaizhixing[index].value +'',
      //           type: "success"
      //         });
      //         // that.$router.go(0);
      //       }
      //     })
      //     .catch(function(error) {
      //       console.log(error);
      //     });

    },
    handleClose(done) {
      this.$confirm("")
        .then(_ => {
          done();
        })
        .catch(_ => {});
    },

    //
    loadAllData() {
    let that = this;
    that.list = [];
    that.tableData = [];
    that.$axios
      .get(that.$url + "/question/getQuestions", {
        params: {
          userId: sessionStorage.getItem("userId")
        }
      })
      .then(function(response) {
        if (response.data.status == 0) {
        console.log(response);
        let value = response.data.object;
        that.list = value;
        //console.log(that.list);
        for (let i in value) {
          //console.log(value[i]);
          that.tableData.unshift(that.dealData(value[i]));
        }
        //this.tableData.unshift(value)
        } else {
            that.$notify({
              title: "",
              message: response.data.msg ,
              type: "warning"
            });
          }

      })
      .catch(function(error) {
        console.log(error);
      });
    },

    //
    theResult (index, row) {
      console.log(index);
      let that = this;
      that.dialogVisible1 = true;
      console.log(that.dialogVisible1);
      that.valueId = that.tableData[index].id;
      that.flashH();
    },

    //
    checkAlgorithm(index,row) {
      let that = this;
      console.log(index);
      console.log(this.yizhixing[index]);
      console.log(this.valueId);

      this.$router.push({
        path: "/Path",
        query: { 
          id: that.valueId,
          key: that.yizhixing[index].key
       }
      });
    }
  },
  mounted() {
    let that = this;
    that.loadAllData();
    console.log(sessionStorage.getItem('userId'))
  //
    // that.$axios
    //   .get(that.$url + "/finalSolution/getOneVersionFinalSolution", {
    //     params: {
    //       questionId : '47',
    //       key : '2',
    //     }
    //   })
    //   .then(function(response) {
    //     console.log(response);
    //   })
    //   .catch(function(error) {
    //     console.log(error);
    //   });

  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.from {
  margin: 20px 100px;
}
.pagination{
  margin-bottom: 100px;
}
.mouseIn {
  cursor: pointer;
}
</style>



